.wrapper {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 1.6rem;
    font-weight: 700;
    line-height: 22px;
    border-radius: 4px;
    min-width: 100px;
    padding: 9px 16px;
    font-family: var(--font-family);
    cursor: pointer;
    background-color: white;
    border: 1px solid transparent;
    user-select: none; //không bôi đen được chữ
    + .wrapper {
        margin-left: 8px;
    }
    &.disable {
        pointer-events: none; //cái ni làm mất hiệu ứng chuột
        opacity: 0.5;
    }
}
.icon + .title,
.title + .icon {
    margin-left: 8px;
}
.icon {
    display: inline-block;
    width: 20px;
    text-align: center;
}
// button tyle
.rounded {
    border-radius: 999px;
    box-shadow: 0 2px 8px rgb(0, 0, 0, 0.06);
    border-color: rgba(22, 24, 35, 0.12);
    &:hover {
        border-color: rgba(22, 24, 35, 0.2);
        background-color: rgba(22, 24, 35, 0.03);
    }
}
.primary {
    color: white;
    background-color: var(--primary);
    &:hover {
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.6)), #fe2c55;
    }
}
.outline {
    color: var(--primary);
    border-color: currentColor;
    &:hover {
        border-color: currentColor;
        background-color: rgba(254, 44, 85, 0.06);
    }
}
.text {
    &:hover {
        text-decoration: underline;
    }
}
// button size
.small {
    min-width: 88px;
    padding: 4px 16px;
}
.large {
    min-width: 150px;
    padding: 14px 16px;
}
